.banner {
  max-width: 21.25rem;
  background-color: #9ceccb;
  border: 2px solid #000;
  border-left: none;
  border-radius: 0 10px 10px 0;
  margin-top: 100px;
  padding: 10px;
  transition: transform .5s;
  transform: translateX(-98%);
}

.is-visible {
  transform: translateX(0);
}

